<template>
  <el-row :gutter="20">
    <el-col :span="12" :offset="6">
      <div class="title">
        <h4>{{ title }}</h4>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="mysql dsn地址" prop="dsnAddr">
            <el-input
              style="width: 80%;"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/dbName"
            ></el-input>
            <el-link
              style="margin-left: 10px;"
              type="primary"
              @click="listTables(true)"
              >获取表名</el-link
            >
          </el-form-item>
          <el-form-item label="表名" prop="tableName">
            <el-select
              style="width: 80%;"
              v-model="ruleForm.tableName"
              placeholder="请选择表名"
              clearable
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%;"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                go.mod文件的module名称，如果
                <b>module名称</b> 与创建web或rpc项目的参数
                <b>module名称</b>
                一样，复制代码到web或rpc项目文件夹下就不需修改import包名。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="嵌入Model">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                生成model代码的表结构体是否嵌入gorm.Model，如果不嵌入gorm.Model，表的列名和struct字段一一对应。
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>
        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebHandler",
  data() {
    return {
      title: "生成Handler代码",
      path: "web-handler",
      helpInfo: `
      <p>根据sql生成handler代码，包含了数据表的增删改查和路由注册。</p>
      <br />
      <p>1. 解压文件，将整个文件夹复制到web项目文件夹下。</p>
      <p>2. 打开终端，执行命令更新swagger文档:  <b style="color: green">make docs</b></p>
      <p>3. 编译和启动服务:  <b style="color: green">make run</b></p>
      <p>4. 复制 <a href="http://localhost:8080/swagger/index.html" target="_blank">http://localhost:8080/swagger/index.html</a> 到浏览器测试<b>增删改查</b>接口。</p>
      `,
      labelPosition: "right",

      ruleForm: {
        dsnAddr: "",
        tableName: "",
        moduleName: "",
        embed: true,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "请输入连接mysql dsn地址",
            trigger: "blur",
          },
        ],
        tableName: [
          { required: true, message: "请选择表名", trigger: "change" },
        ],
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
      },

      tableNames: [],
    };
  },
  created: function() {
    const apiUrl = this.GLOBAL.BASE_URL + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.tableName = data.data.tableName;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.embed = data.data.embed;

          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = this.GLOBAL.BASE_URL + "/generate";
          const arg = `web handler --module-name=${this.ruleForm.moduleName} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${this.ruleForm.tableName} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = { dsnAddr: this.ruleForm.dsnAddr };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = this.GLOBAL.BASE_URL + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
      };
      axios
        .post(apiUrl, formdata)
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("获取 table 成功")
              : this.$message.error("获取 table 失败");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          this.$message.error("获取 table 失败");
          // console.log(err);
        });
    },
    selectBlur(e) {
      // 意见类型
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 1px solid #ebebeb;
  box-shadow: 0px 0px 10px #ebebeb;
  border-radius: 5px;
  padding: 40px 40px;
}

el-upload {
  /* width: 125%; */
  height: 100%;
}
.el-upload .el-upload-dragger {
  /* width: 125%; */
  height: 100%;
}
.el-upload-dragger .el-icon-upload {
  margin: 16px 0 16px;
}
</style>



