<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;">
        <a href="https://github.com/zhufuyi/sponge" target="_blank">sponge</a>
        是一个快速创建web服务和微服务工具，也是一个微服务框架，sponge拥有丰富的生成代码命令，常用的重复代码和脚本是自动生成，加上代码解耦模块化设计，很容易构建出从开发到部署的完整代码工程，从而提高开发效率。
      </p>
      <p>
        github：<a href="https://github.com/zhufuyi/sponge" target="_blank"
          >https://github.com/zhufuyi/sponge</a
        >
      </p>
      <p>
        使用文档：<a
          href="https://blog.csdn.net/zhuyasen/article/details/128336254"
          target="_blank"
          >https://blog.csdn.net/zhuyasen/sponge</a
        >
      </p>
      <br />
      <p>生成代码框架图：</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>代码对应的服务框架图：</p>
      <div class="microservices-image">
        <el-image :src="mircoServiceFrameworkImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}
</style>



