<template>
  <el-row :gutter="20">
    <el-col :span="12" :offset="6">
      <div class="title">
        <h4>{{ title }}</h4>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="27%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="yaml文件" prop="yamlFile">
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".yaml,.yml"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或<em>点击选择文件</em>
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                只能上传yaml文件
              </div> -->
            </el-upload>
          </el-form-item>

          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "YamlConfig",
  data() {
    return {
      title: "生成config代码",
      path: "yaml-config",
      helpInfo: `
      <p>把yaml配置转为go struct，并包含读取yaml配置的代码。</p>
      `,

      labelPosition: "right",
      fileList: [],
      ruleForm: {
        yamlFile: "",
      },
      rules: {
        yamlFile: [
          { required: true, message: "请选择yaml文件", trigger: "change" },
        ],
      },
    };
  },
  created: function() {
    const apiUrl = this.GLOBAL.BASE_URL + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.yamlFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 上传文件
          let fd = new FormData();
          // fd.append("param", "param value");
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = this.GLOBAL.BASE_URL + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                // 下载代码
                const apiUrl = this.GLOBAL.BASE_URL + "/generate";
                const arg = `config --yaml-file=${res.data.data}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = { dsnAddr: this.ruleForm.dsnAddr };
    },
    handleOnchange(file, fileList) {
      this.ruleForm.yamlFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("yamlFile");
      }
    },
    handleOnRemove(file, fileList) {
      if (fileList.length === 0) {
        this.ruleForm.yamlFile = "";
        this.$refs.ruleForm.validateField("yamlFile");
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 1px solid #ebebeb;
  box-shadow: 0px 0px 10px #ebebeb;
  border-radius: 5px;
  padding: 40px 40px;
}

el-upload {
  /* width: 125%; */
  height: 80%;
}
.el-upload .el-upload-dragger {
  /* width: 125%; */
  height: 80%;
}
.el-upload-dragger .el-icon-upload {
  margin: 16px 0 16px;
}
</style>



