<template>
  <el-row :gutter="20">
    <el-col :span="12" :offset="6">
      <div class="title">
        <h4>{{ title }}</h4>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%;"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                go.mod文件的module名称，如果 <b>module名称</b> 与创建web或rpc项目的参数
            <b>module名称</b>
            一样，复制代码到web或rpc项目文件夹下就不需修改import包名。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="rpc服务名称" prop="rpcServerNames">
            <el-input
              style="width: 80%;"
              v-model.trim="ruleForm.rpcServerNames"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content">
                多个rpc服务名称，用逗号分隔。
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>
        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroRPCClient",
  data() {
    return {
      title: "生成rpc客户端代码",
      path: "micro-rpc-cli",
      helpInfo: `
      <p>生成rpc客户端代码包括了服务注册和多种服务治理功能。</p>
      `,
      labelPosition: "right",

      ruleForm: {
        moduleName: "",
        rpcServerNames: "",
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
        rpcServerNames: [
          { required: true, message: "请输入rpc服务名称", trigger: "blur" },
        ],
      },
    };
  },
  created: function() {
    const apiUrl = this.GLOBAL.BASE_URL + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.rpcServerNames = data.data.rpcServerNames;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = this.GLOBAL.BASE_URL + "/generate";
          const arg = `micro rpc-cli --module-name=${this.ruleForm.moduleName} --rpc-server-name=${this.ruleForm.rpcServerNames}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {};
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 1px solid #ebebeb;
  box-shadow: 0px 0px 10px #ebebeb;
  border-radius: 5px;
  padding: 40px 40px;
}

el-upload {
  /* width: 125%; */
  height: 100%;
}
.el-upload .el-upload-dragger {
  /* width: 125%; */
  height: 100%;
}
.el-upload-dragger .el-icon-upload {
  margin: 16px 0 16px;
}
</style>



